import { Button, Grid, Typography, Link } from '@mui/material'
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import React from 'react'

const Footer = () => {
    return (
        <div>
            <Grid className='bg-black text-white text-center mt-10'
                container
                sx={{ bgcolor: "black", color: "white", py: 3 }}
            >
                <Grid item xs={12} sm={6} md={3} >
                    <Typography className='pb-5' variant='h6' >Company</Typography>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>About</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Blog</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Press</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Career</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Partner</Button>
                    </div>
                </Grid>
                <Grid item xs={12} sm={6} md={3} >
                    <Typography className='pb-5' variant='h6' >Solutions</Typography>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Marketing</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Analytics</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Commerce</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Insights</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Supports</Button>
                    </div>
                </Grid>
                <Grid item xs={12} sm={6} md={3} >
                    <Typography className='pb-5' variant='h6' >Documentation</Typography>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Guides</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>API Status</Button>
                    </div>
                </Grid>
                <Grid item xs={12} sm={6} md={3} >
                    <Typography className='pb-5' variant='h6' >Legal</Typography>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Claim</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Privecy</Button>
                    </div>
                    <div>
                        <Button className='pb-5' variant='h6' gutterBottom>Terms</Button>
                    </div>

                </Grid>

                <Grid className='pt-20' item xs={12} >
                    <Typography variant="body2" component="p" align="center">
                        &copy; 2024 Avi Logistic Pvt.Ltd. All rights reserved.
                    </Typography>
                    <Typography variant="body2" component="p" align="center">
                        Made with love by Avishek.
                    </Typography>
                    <Typography variant="body2" component="p" align="center">
                        Connect with me : {' '}
                        <Link href="https://www.linkedin.com/in/avishek-verma/" target="_blank" color="inherit" underline="always">
                            <LinkedInIcon/>
                        </Link>{' '}
                        / {' '}
                        <Link href="https://avishekverma.github.io/Personal_Portfolio/" target="_blank" color="inherit" underline="always">
                            www.avishekverma.com
                        </Link>
                    </Typography>
                </Grid>


            </Grid>
        </div>
    )
}

export default Footer