import React from 'react'

function HomeSectionCard({product}) {
  return (
    <div className='cursor-pointer flex flex-col items-center bg-white round-lg shadow-lg 
    oveerflow-hidden w-[15rem] mx-3 border '>
        <div className='h-[13rem] w-[10rem'>
            <img className='object-cover object-top w-full h-full' src={product.imageUrl}/>
            
        </div>
        <div className='p-4'>
          <h3 className='text-lg font-medium text-gray-900'>{product.brand}</h3>
          <p className='mt-2 text-sm text-gray-500'>Men Solid Pure Cotton Straight Kurta</p>
        </div>
    </div>
  )
}

export default HomeSectionCard